.container {
    flex-direction: column;
    justify-content: center;

}

.title {
    background-color: #fc5b81;
}

.projectsList {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 auto;
    /*align-items: center;*/
}

@media (max-width: 879px) {
    .projectsList {
        justify-content: center;
    }
}

